@import '../components/colors';

#content-wrapper {
  padding: 0;
  z-index: 5;
  position: relative;
  // box-shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.4);
}

.white {
  background: #f8f8f8;
  padding: 20px 0;
}

.light-prpl{
  background: $lightPurpl;
  color: white;
}

#content-wrapper section {
  padding-top: 30px; 

}

#footer-wrapper {
  z-index: 1;
  padding-top: 60px;
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #202020;
}

.border-hr hr{
  border-top: 1px solid black;
}

.widget-img {
  max-width: 80px;
  height: auto;
}

.about-us-widget {
  background-size: contain;
}
#lavender-slider{
  background: white;
}

.widget-title {
  font-size: 18px;
  text-transform: uppercase;
}

.testimonial-quote blockquote {
  border: 0;
  margin: 0;
  padding: 0;
  background: none;
  font-family: 'TTNorms-Meduim';
  font-size: 1.5em;
  font-style: italic;
  line-height: 1.4 !important;
  margin: 0;
  position: relative;
  text-shadow: 0 1px white;
  z-index: 600;
}

.testimonial-quote blockquote * {
  box-sizing: border-box;
}

.testimonial-quote blockquote p {
  color: $darkPurple;
  line-height: 1.4 !important;
}

.testimonial-quote blockquote p:first-child:before {
  content: '\201C';
  color: $darkPurple;
  font-size: 7.5em;
  font-weight: 700;
  opacity: .3;
  position: absolute;
  top: -.4em;
  left: -.2em;
  text-shadow: none;
  z-index: -300;
}

.center {
  text-align: center;
}

#portfolio{
  padding-bottom: 6em;
  @media(max-width: 768px) {
    padding-bottom: 1em;
  }
}

h2.main-title-header{
  background: #a89ac0;
  margin-top: 0;  
  padding: 0.5em;
  color: white;
  padding-left: 2.5em;
}
.prpl-title{
  color: $darkPurple;
}

.sub-main-title{
  margin: 0;
}

.natural-img-container{
  .natural-img{
    @media(max-width: 768px) {
     display: none;  
    }
  }
}
.natural-img-container-mobile{
  @media(max-width: 768px) {
    width: 100%;
    text-align: center;
    background: $lightPurpl
   
   }
   @media(min-width: 769px) {
    display: none;
   }
 
  .natural-img{
    @media(max-width: 768px) {
      padding: 1em;     
      height: 12em;
      margin-top: 2em;
      background: white;
      border-radius: 50%;
   }
  }
}


#content-wrapper section#stats {  
  padding-top: 20px; 
  color: $lightPurpl;
  border-bottom: 1px solid $lightPurpl;
  background: white;

  .stat-icon {
   color: $darkgrey;
   color: $darkPurple;
   font-size: 44px;  
  }
  .stat-icon.prpl{
    color: $darkPurple;
  }
}
#content-wrapper section#stats.prpl{
  color: $darkgrey;
}

#stats {
  padding-bottom: 20px;
  h1 {
    margin: 10px 0;
    font-size: 32px;
  }
  h3 {
    margin: 0;
    font-size: 16px;
    text-transform: uppercase;

  }
  @media(max-width: 768px) {
    h3 {
      font-size: 14px;
    }
  }
}

.divider-section {
  position: relative;
  overflow: hidden;
  display: block;
  color: black;
  a {
    color: #f5f5f5;
  }
}

.img-production-container {

  div[class^="col-"] {
    padding-left: 9px;
    padding-right: 9px;

    .img-prod {
      position: relative;
      height: 30em;

      @media(max-width: 768px) {
        height: 18em;
        margin-bottom: 2em; 
       }

      h5 {
        position: absolute;
        margin: auto;
        text-align: center;
        z-index: 24;
        width: 100%;
        font-size: 1.5em;  
        font-weight: 300;
        padding: 0.5em;
        color: #fcfcfc;
        text-shadow: 0px 4px 3px rgb(36, 20, 58), 0px 8px 13px rgb(28, 9, 53), 0px 18px 23px rgb(38, 18, 64);
        display: none;
        top: 50%;
        transform: translateY(-50%);

      }
      .production-img {
        height: 100%;
        background-size: cover;
        background-repeat: no-repeat;          

        .img-transperant {
          height: 100%;
          position: relative;
          line-height: 16;
          color: #f8f8f8;
          text-align: center;
          background: #000;
          background: -moz-linear-gradient(-45deg, #000000 0%, #000000 25%, #1e539e 50%, #ff3083 75%, #7800a8 100%);
          background: -webkit-linear-gradient(-45deg, #000000 0%, #000000 25%, #1e539e 50%, #ff3083 75%, #7800a8 100%);
          background: linear-gradient(135deg, #000000 0%, #000000 25%, #dcd277 50%, #1e0233 75%, #7800a8 100%);
          background-size: 400% 400%;
          cursor: pointer;
          transition: .8s all;
          mix-blend-mode: screen;
        }
        @supports (-ms-ime-align:auto) {
          .img-transperant { opacity: 0.1; } 
        }
      }

      &:hover h5 {
        display: block;
      }
      &:hover .img-gradient {
        background: rgba(37, 18, 56, 0.82);
        width: 100%;
        height: 100%;
        top: 0;
        transition: .8s all;
        position: absolute;
      }

      &:hover .img-transperant {
        background-position: 100% 100%;
      }

    }
  }
}

.front.card{
  position: relative;

    .more-icon{
      position: absolute;
      margin: auto;
      text-align: center;
      z-index: 24;
      width: 100%;
      font-size: 1.5em;  
      font-weight: 300;
      padding: 0.5em;
      color: #fcfcfc;
      text-shadow: 0px 4px 3px rgb(36, 20, 58), 0px 8px 13px rgb(28, 9, 53), 0px 18px 23px rgb(38, 18, 64);
      display: none;
      top: 50%;
      transform: translateY(-50%);

    }
    .img-transperant {
      height: 100%;
      position: relative;
      line-height: 16;
      color: #f8f8f8;
      text-align: center;
      background: #000;
      background: -moz-linear-gradient(-45deg, #000000 0%, #000000 25%, #1e539e 50%, #ff3083 75%, #7800a8 100%);
      background: -webkit-linear-gradient(-45deg, #000000 0%, #000000 25%, #1e539e 50%, #ff3083 75%, #7800a8 100%);
      background: linear-gradient(135deg, #000000 0%, #000000 25%, #dcd277 50%, #1e0233 75%, #7800a8 100%);
      background-size: 400% 400%;
      cursor: pointer;
      transition: .8s all;
      mix-blend-mode: screen;
    }
    @supports (-ms-ime-align:auto) {
      .img-transperant { opacity: 0.1; } 
    }
  
  &:hover .more-icon {
    display: block;
    cursor: pointer;
    i{
      padding: 0.6em 0.66em;
      border: 1px solid white;
      border-radius: 50%;
    }
  }
  &:hover .img-gradient {
    background: rgba(37, 18, 56, 0.82);
    width: 100%;
    height: 100%;
    top: 0;
    transition: .8s all;
    position: absolute;
  }

  &:hover .img-transperant {
    background-position: 100% 100%;
  }
}

.production-img {
  height: 17em;
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 2em;

}

.team-image span {
  display: inline-block;
  font-size: 1.1em;
  height: 3.5em;
